.simple-intro {
  position: relative;
  z-index: 0;

  &__light {
    position: absolute;
    top: 53%;
    left: 50%;
    z-index: -1;

    width: 450px;
    max-width: 100%;
    height: 147px;

    transform: translate(-50%, -50%) translateZ(0);
    filter: blur(55px);

    @include vp-767 {
      top: 46%;

      width: 300px;
    }

    @include vp-374 {
      width: 80%;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: -1;

      background: url("../img/svg/lights/light.svg") no-repeat center;
      background-size: 100% 100%;
      transform: translateZ(0);
    }
  }

  &__grid {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 78px 0 76px;
    gap: 28px;

    @include vp-1023 {
      padding: 64px 0 70px;
    }

    @include vp-767 {
      padding: 50px 0 62px;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;

      width: 100%;
      height: 1px;

      background-image: $gradient;
      opacity: 0.5;
    }
  }

  .title.title--h1 {
    @include vp-767 {
      font-size: 40px;
      line-height: 1.3;
      letter-spacing: -0.2px;
    }
  }
}
